<template>
  <div class="vh-100 d-flex flex-column">
    <div class="main-input-form my-5">
      <input
      v-model="this.taskInput"
      type="text"
      name="inputFormTask"
      id="inputFormTask"
      placeholder="Adicionar Tarefa"
      class="form-control w-25"
      />
      <button 
      @click="addTask"
      class="btn btn-primary"
      >Adicionar Tarefa</button>
    </div>
    <div class="table-responsive">
      <table class="table table-striped align-middle">
        <thead class="table-light">
          <tr>
            <th>Tarefa</th>
            <th>Status</th>
            <th>Ações</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(task, index) in arrayTasks">
            <th class="fw-normal">{{ task.name }}</th>
            <th class="fw-normal">{{ task.status }}</th>
            <th class="d-flex gap-2">
              <button 
              :value="index" 
              @click="() => handleShowEditForm(index, task.name)"
              class="btn btn-secondary btn-sm"
              >Editar</button>
              <button 
              :value="index" 
              @click="removeTask"
              class="btn btn-danger btn-sm"
              >Apagar</button>
            </th>
          </tr>
        </tbody>
      </table>
    </div>
    <div class="main-input-form" v-show="showEditForm">
      <label for="inputFormTaskEdited">Editar Tarefa</label>
      <input
      v-model="this.taskInputEdited"
      type="text"
      name="inputFormTaskEdited"
      id="inputFormTaskEdited"
      class="form-control w-25"
      />
      <select 
      name="status" 
      id="status" 
      v-model="this.statusEdited"
      class="form-select w-25"
      >
      <option value="Concluído">Concluído</option>
      <option value="Em Andamento">Em Andamento</option>
      <option value="Aguardando Inicio">Aguardando Inicio</option>
    </select>
    <button @click="editTask"
    class="btn btn-primary"
    >
    Salvar Edição
    </button>
    </div>
  </div>
  </template>

<script>

export default {
  
  name: "App",
  
  mounted() {
    if (localStorage.getItem("tasks")) {
      this.arrayTasks = JSON.parse(localStorage.getItem("tasks"));
    } else {
      localStorage.setItem("tasks", [])
    }
  },

  data() {
    return {
      taskInput: "",
      arrayTasks: [],
      indexTaskToEdit: null,
      taskInputEdited: "",
      showEditForm: false,
      statusEdited: "Aguardando Inicio"
    };
  },

  methods: {
    addTask() {
      const dataTask = { name: this.taskInput, status: "Aguardando Inicio" };
      this.arrayTasks.push(dataTask);
      this.taskInput = "";
      localStorage.setItem("tasks", JSON.stringify(this.arrayTasks));
    },
    handleShowEditForm(index, nameTask) {
      this.showEditForm = !this.showEditForm
      this.indexTaskToEdit = index
      this.taskInputEdited = nameTask
    },
    removeTask(e) {
      const {
        target: { value },
      } = e;
      this.arrayTasks.splice(value, 1);
      localStorage.setItem("tasks", JSON.stringify(this.arrayTasks));
    },
    editTask() {
      const dataTask = {name: this.taskInputEdited, status: this.statusEdited}
      this.arrayTasks.splice(this.indexTaskToEdit, 1, dataTask)
      localStorage.setItem("tasks", JSON.stringify(this.arrayTasks));
      this.showEditForm = false
    },
  },
};
</script>

<style>

  .main-input-form {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1rem;
}
</style>
